.App {
  
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
}

.App-header .quizz{
  border: 1px solid white;
  width:500px;
  padding: 30px 0;
}

.App-header .quizz h5{
  margin: 0;
  text-align: center;
  padding-bottom: 10px;
}

.App-header .quizz .question{
  background: #a63434;
  padding: 10px 0;
  text-align: center;
}

.App-header .quizz .answer-area{

}

.App-header .quizz .answer-area ul{
  padding: 0;
}

.App-header .quizz .answer-area ul li{
  list-style: none;
  padding:5px 20px ;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  

}


.App-header .quizz .answer-area ul li:hover{
  background: grey;
}

.App-header .quizz .answer-area ul li:active{
  background: #846767;
}


.App-header .nextQuestion{
  padding: 10px 20px;
  margin-top:30px;
  background: black;
  color:white;
  outline: none;
}

.App .App-header{
  z-index: 10;
}

.App .full_screen{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 100;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
